import Image from "next/image";
import React from "react";
import Logo from "../../public/Logo.png";
import { Helvetical } from "@/pages/fonts/fonts";

const Navbar = () => {
  const nav = [
    { name: "Home", path: "", active: "" },
    { name: "About", path: "about", active: "" },
    { name: "Contact", path: "contact", active: "" },
    { name: "Login", path: "login", active: "" },
  ];
  return (
    <nav className="w-full flex p-5 items-center justify-between text-black text-[18px] font-medium ">
      <div className="flex items-center gap-2">
        <Image alt="" src={Logo} className="w-16 h-16"/>
        <p className={`${Helvetical.variable}`}>BLOCKCHAINFUL</p>
      </div>

      <ul className="flex items-center justify-between w-1/5">
        {nav.map((links, i) => (
          <li key={i}>
            <a href={links.path} className={`${links.active}`}>
              {links.name}
            </a>
          </li>
        ))}
      </ul>

      <div>Explore</div>
    </nav>
  );
};

export default Navbar;
